import styled from 'styled-components';


const ZoneListContainer = styled.div`
    margin-top:135px;

    @media (max-width: 768px) {
        margin-top:0;
    }
`

const ZoneItemContainer = styled.li`
    display:flex;
    justify-content:space-around;
    align-items: center;
    padding-top:100px;
    padding-bottom:100px; 

    @media (max-width: 768px) {
        flex-direction:column;
        padding:0;
        margin:0;
    }    

    & div {
        display:inline-block;
    }

    & div:nth-child(1){
        width:35%;
        @media (max-width: 768px) {
            width:100%;   
            margin-top:30px;         
        }
    }

    & div:nth-child(1) img{
        width:100%;    
    }

    & div:nth-child(2){
        width:60%;
        @media (max-width: 768px) {      
            width: 100%;      
        }        
    }
    & h4 {
        font-size:24px;
        font-weight:500;
        margin-top:45px;
        margin-bottom:45px;

        @media (max-width: 1440px) {
            margin-top:0;
            margin-bottom:15px;
            font-size:16px;
            text-align:center;
            padding-top:10px;
        }
    }

    & span {
        display:inline-block;
        font-size:16px;
        line-height:24px;
        word-break:keep-all;

        @media (max-width: 1440px) {
            display: inline;    
            font-size:12px;        
        }             

        @media (max-width: 768px) {     
            display: inline-block;   
            line-height:18px;
        }
    }

    br{
        display:block;
        height:50px;
        width:100%;

        @media (max-width: 768px) {      
            display:none;
        }  
    }

    .margin{
        height:10px;
    }           
`

function ZoneItem({data}){
    return (
        <ZoneItemContainer id={data.id}>
            <div>
                <img src={data.img1} alt={data.name}/>
                <img src={data.img2} alt={data.name}/>
            </div>
            <div className="text">
                <h4>{data.name}</h4>
                {
                    data.text1.split('\n').map( line => {
                        return (<span>{line}<br /></span>)
                    })
                }
                <p className="margin"/>
                {data.text2 ?
                   data.text2.split('\n').map( line => {
                    return (<span>{line}<br /></span>)
                }) : ''
                }
                <p className="margin"/>
                {data.text3 ?
                   data.text3.split('\n').map( line => {
                    return (<span>{line}</span>)
                }) : ''
                }
            </div>
        </ZoneItemContainer>
    )
}


