import React from 'react';
import about1 from '../images/about1.png';
import about2 from '../images/about2.png';
import about3 from '../images/about3.png';
import about4 from '../images/about4.png';
import about5 from '../images/about5.png';
import about6 from '../images/about6.png';
import styled from 'styled-components';


const ZoneListContainer = styled.div`

    @media (max-width: 768px) { 
        margin:0 auto;
        /* width:90%; */
    }

    & ul {
        display:flex;
        flex-wrap: wrap;
        justify-content:space-between;

        @media (max-width: 768px) { 
            /* flex-direction:column; */
        }
    }
`

const ZoneItemContainer = styled.li`
    width:490px;
    height:520px;
    position: relative;

    @media (max-width: 768px) { 
        width:100%;
        height:auto;
        /* display:flex; */
        padding-top:30px;
    }

    & .body {
        @media (max-width: 768px) { 
            /* margin-left:10px; */
            margin-top:10px;
        }
    }

    .over {
        position:absolute;
        top:0;
        left:0;
        opacity: 0;        
    }

    & .img .crop {
        @media (max-width: 768px) { 
            /* width:135px; */
            height:100px;
            overflow:hidden;
        }            
    }

    & .img img {
        @media (max-width: 768px) { 
            width:100%;
            /* height:80px; */
        }
    }

    & .img:after {
        content: '${props => props.name}';
        color:#fff;
        font-size:30px;
        text-align:center;
        font-weight:700;
        line-height:264px;
        width:100%;
        height:264px;
        background-color:rgba(0,0,0,0.5);
        position: absolute;
        top:0;
        left:0;
        opacity: 0;
        transition: all 0.6s;

        @media (max-width: 768px) { 
            display:none;
        }
    }

    & .img:hover:after{
        opacity: 1;
    }

    & h4{
        font-size:24px;
        font-weight:500;
        margin-top:45px;
        margin-bottom:15px;

        @media (max-width: 768px) { 
            margin:0;
            font-size:14px;
            padding-bottom:5px;
        }        
    }

    & p {
        font-size:16px;
        color:#444;

        @media (max-width: 768px) { 
            margin:0;
            font-size:12px;
        }         
    }
`

function ZoneItem({data}){
    return (
        <ZoneItemContainer name={data.name}>
            <div className="img">
                <div className="crop">
                    <img src={data.img} alt={data.name}/>
                </div>
                <div className="over">
                    {data.name}
                </div>
            </div>
            <div className="body">
                <h4>{data.name}</h4>
                <p>
                    {data.text}
                </p>
            </div>
        </ZoneItemContainer>
    )
}


function ZoneList() {
    const zoneData = [
        {name : "   ", text : "  ,      .", img : about1},
        {name : " ", text : "      .     ,           .", img : about2},
        {name : " ", text : " ,  50  ,  ,   ,          ,   ,       .", img : about3},
        {name : "  ", text : "           .          ,          .           .", img : about4},
        {name : " ", text : ",               .", img : about5},
        {name : " ", text : "        .         .", img : about6},
    ]
  return (
    <ZoneListContainer>
        <ul>
            {zoneData.map((zoneData)=> {
                console.log(zoneData);
                    return (
                        <ZoneItem data = {zoneData}/>
                    )
                })
            }
        </ul>
    </ZoneListContainer>
  );    
}

export default ZoneList;